import classes from "./TemplateDetail.module.css";
import React, { useEffect, useState } from "react";
import { getAllElements, getAllTemplates } from "../../../../api/Template";
import Tabs from "../Tabs/Tabs";
import { Link } from "react-router-dom";

const TemplateDetail = () => {
  const [templates, setTemplates] = useState(null);
  const [elements, setElements] = useState(null);

  useEffect(() => {
    getAllTemplates()
      .then((data) => {
        if (data.message) {
          setTemplates(data.data);
        } else {
          alert("No templates found");
        }
      })
      .catch((err) => console.log(err));

    getAllElements()
      .then((res) => {
        console.log(res);
        if (res.message) {
          setElements(res.data);
        } else {
          alert("No Elements found");
        }
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <section className={classes.TemplateDetail}>
      <h1 style={{ margin: "10px" }}>You successfully updated Template</h1>
      <button style={{ margin: "20px" }}>
        <Link to="/create/template">Upload new template</Link>
      </button>
      <Tabs>
        <div label="Your templates">
          {templates ? (
            templates.map((template) => {
              return (
                <div className={classes.Tab}>
                  <img width="100%" src={template.src} alt="template" />
                  <h3>{template.name}</h3>
                </div>
              );
            })
          ) : (
            <h1>Loading....</h1>
          )}
        </div>
        <div label="Your Elements">
          {elements ? (
            elements.map((element) => {
              return (
                <div className={classes.Tab}>
                  <img width="20%" src={element.src.png} alt="template" />
                  <h3>{element.element_name}</h3>
                </div>
              );
            })
          ) : (
            <h1>Loading....</h1>
          )}
        </div>
      </Tabs>
    </section>
  );
};

export default TemplateDetail;
